body{
    background: #9266ff;
}

.Container{
    background: #3f3160;
    min-height: 500px;
    width: 400px;
    margin: 40px auto;
}
#to-do-form input{
    background: rgb(2,112,137);
    border: 0;
    width: 200px;
    height: 50px;
    padding: 0 20px;
    margin: 20px;
    padding: 0 20px;
    font-size: 18px;
    border-radius: 10px;
    color: #fff;
}
#to-do-form input::placeholder{
    color: rgba(255,255,255,0.5);
  }
  #to-do-form input:focus{
    outline: none;
  }
  #to-do-form button{
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color:rgba(202, 156, 24, 1); 
    cursor: pointer;
    color: #fff;
    font-weight: bolder;
  }
  #to-do-form button:focus{
    outline: none;
  }